<template>
  <div class="container">
    <Header title="Task Tracker" @toggle-add-task="toggleAddTask" :showAddTask="ShowForm"/>
     <div v-show="ShowForm">
       <AddTask @add-task="TaskFormSubmit"/>
     </div>
    <Tasks @toggle-reminder="toggleReminder" @delete-task="DeleteTask" :tasks="tasks"/>
  </div>
</template>

<script>
import Header from './components/Header.vue';
import Tasks from './components/Tasks.vue';
import AddTask from './components/AddTask.vue';

export default {
  name: 'App',
  components: {
    Header,Tasks,AddTask
  },
  data() {
    return {
      tasks:[],
      ShowForm:false
    } 
  },
  methods: {
    DeleteTask(id){
      if(confirm('Are you sure?')){
        this.tasks = this.tasks.filter((task) => task.id!==id);
      }else{
        alert("Task is safe.");
      }
    },
    toggleAddTask(){
        this.ShowForm = !this.ShowForm;
    },
    toggleReminder(id){
      this.tasks = this.tasks.map((task) => task.id === id ? {
        ...task,reminder: !task.reminder
      }:task);
    },
    TaskFormSubmit(task){
      this.tasks = [...this.tasks,task];
    }
  },  
  created() {
    this.tasks = [
      {
        id:1,
        text:"Task 1",
        day:'March 1st at 2:30pm',
        reminder:true
      },
      {
        id:2,
        text:"Task 2",
        day:'March 3rd at 2:30pm',
        reminder:false
      },
      {
        id:3,
        text:"Task 3",
        day:'March 4th at 2:30pm',
        reminder:true
      },
      {
        id:4,
        text:"Task 4",
        day:'March 1st at 10.00pm',
        reminder:true
      },
      {
        id:5,
        text:"Task 5",
        day:'March 10th at 8:23am',
        reminder:false
      },
    ];
  },
}
</script>
